import { deleteValueLS } from "./local_storage_promises";

export const clearData = async () => {
  if (process.env.REACT_APP_PLATFORM === "chrome") {
    chrome.action.setBadgeText({ text: "" });
  } else {
    browser.browserAction.setBadgeText({ text: "" });
  }
  await deleteValueLS("token");
  await deleteValueLS("liveStreams");
  await deleteValueLS("currentUserId");
};
