import type { IGetFollowedStream, IGetFollowedStreams } from "./interfaces";

export const fetchFollowedStreams = async (
  token: string,
  currentUserId: string
): Promise<IGetFollowedStream[]> => {
  let cursor: string = "";
  let followedStreams: IGetFollowedStream[] = [];
  const fetchAllFollowedStreams = async (
    cursor: string,
    followedOnlineStreams: IGetFollowedStream[]
  ): Promise<IGetFollowedStream[]> => {
    const result = await fetch(
      `https://api.twitch.tv/helix/streams/followed?user_id=${currentUserId}&first=100&after=${cursor}`,
      {
        method: "GET",
        headers: {
          "client-id": process.env.REACT_APP_CLIENT_ID!,
          Authorization: `Bearer ${token}`,
        },
      }
    );
    if (!result.ok) {
      if (result.status === 401) {
        throw new Error("Invalid Token");
      }
      throw new Error("Fetch Followed Streams Failed");
    }
    const json: IGetFollowedStreams = await result.json();
    const currentFollowedStreams: IGetFollowedStream[] = json.data.map((c) => {
      return {
        game_name: c.game_name,
        thumbnail_url: c.thumbnail_url,
        title: c.title,
        user_id: c.user_id,
        user_login: c.user_login,
        user_name: c.user_name,
        viewer_count: c.viewer_count,
      };
    });
    if (json.pagination?.cursor) {
      return await fetchAllFollowedStreams(json.pagination.cursor, [
        ...followedOnlineStreams,
        ...currentFollowedStreams,
      ]);
    }
    return [...followedOnlineStreams, ...currentFollowedStreams];
  };
  followedStreams = await fetchAllFollowedStreams(cursor, followedStreams);
  return followedStreams;
};
