import type { IGetUser } from "./interfaces";

// Limitation of twitch API
const MAXNUMBER = 99;

export const fetchUsersInfos = async (
  token: string,
  usersLogins: string[]
): Promise<IGetUser[]> => {
  const promises: Promise<IGetUser[]>[] = [];
  for (let i = 0; i < usersLogins.length; i += MAXNUMBER) {
    promises.push(
      fetch(
        `https://api.twitch.tv/helix/users?login=${usersLogins
          .slice(i, i + MAXNUMBER)
          .join(`&login=`)}`,
        {
          method: "GET",
          headers: {
            "client-id": process.env.REACT_APP_CLIENT_ID!,
            Authorization: `Bearer ${token}`,
          },
        }
      ).then(async (res) => {
        if (!res.ok) {
          if (res.status === 401) {
            throw new Error("Invalid Token");
          }
          throw new Error("Fetch Users Infos Failed");
        }
        return (await res.json()).data;
      })
    );
  }
  return (await Promise.all(promises)).flat();
};
