import {
  FetchAllFollowedResult,
  LoginResult,
  LogoutResult,
  Message,
  Query,
} from "./interfaces";
import { fetchAllFollowedLogins } from "./requests/fetchAllFollowedLogins";
import { fetchFollowedStreams } from "./requests/fetchFollowedStreams";
import {
  getFavoritesLS,
  getValueLS,
  setValueLS,
} from "./utils/local_storage_promises";
import type {
  IGetFollowedStream,
  IGetFollowedWithImage,
} from "./requests/interfaces";
import { fetchUsersInfos } from "./requests/fetchUsersInfos";
import { clearData } from "./utils/clearData";
import { login } from "./requests/login";
import { logout } from "./requests/logout";

const pollInterval = 2.5;
const badgeBackgroundColor = "#5c3596";
const badgeTextColor = "#ffffff";

if (process.env.REACT_APP_PLATFORM === "chrome") {
  chrome.action.setBadgeBackgroundColor({
    color: badgeBackgroundColor,
  });
} else {
  browser.browserAction.setBadgeBackgroundColor({
    color: badgeBackgroundColor,
  });
  browser.browserAction.setBadgeTextColor({
    color: badgeTextColor,
  });
}

chrome.runtime.onStartup.addListener(() => {
  pollFetchFollowedStreams(true);
});

chrome.alarms.create({ periodInMinutes: pollInterval });

chrome.alarms.onAlarm.addListener(() => {
  pollFetchFollowedStreams(false);
});

const pollFetchFollowedStreams = async (firstCall: boolean) => {
  try {
    const token = await getValueLS<string>("token");
    if (!token) {
      return;
    }
    const currentUserId = await getValueLS<string>("currentUserId");
    const followedSt = await fetchFollowedStreams(token, currentUserId);
    const userInfo = await fetchUsersInfos(
      token,
      followedSt.map((r) => r.user_login)
    );
    const followedWithImage: IGetFollowedWithImage[] = [];
    followedSt.forEach((f) => {
      followedWithImage.push({
        ...f,
        user_image: userInfo.find((u) => u.id === f.user_id)?.profile_image_url,
      });
    });
    const notification = await getValueLS<string>("notification");
    if (!firstCall && Boolean(notification)) {
      await computeNewStreamsNotification(followedSt);
      await setValueLS("liveStreams", followedWithImage);
    } else {
      await setValueLS("liveStreams", followedWithImage);
    }
    setBadgeFav(followedSt);
  } catch (error: any) {
    if (error.message === "Invalid Token") {
      await clearData();
    }
  }
};

const setBadgeFav = async (followedSt: IGetFollowedStream[]) => {
  const favorites = await getFavoritesLS();
  const nbFaStreamsLive = followedSt.filter((f) => {
    return favorites.includes(f.user_id);
  });
  if (process.env.REACT_APP_PLATFORM === "chrome") {
    chrome.action.setBadgeText({ text: nbFaStreamsLive.length.toString() });
  } else {
    browser.browserAction.setBadgeText({
      text: nbFaStreamsLive.length.toString(),
    });
  }
};

const computeNewStreamsNotification = async (
  fetchedLiveStreams: IGetFollowedStream[]
) => {
  const favorites = await getFavoritesLS();
  const storedLiveStreams = await getValueLS<IGetFollowedWithImage[]>(
    "liveStreams"
  );
  // Computes new live streams by doing a diff between the fetched and the stored ones
  const newLiveStreams = fetchedLiveStreams
    .filter((l) => {
      return !storedLiveStreams.some((s) => s.user_id === l.user_id);
    })
    .filter((l) => {
      // Receive notifs only for favorite streams
      return favorites.some((f) => f === l.user_id);
    });
  if (newLiveStreams.length !== 0) {
    let streamerLogins = "";
    newLiveStreams.forEach((n, i) => {
      if (i === 0) {
        streamerLogins += n.user_name === "" ? n.user_login : n.user_name;
      } else {
        streamerLogins += `, ${
          n.user_name === "" ? n.user_login : n.user_name
        }`;
      }
    });
    chrome.notifications.create(`TFS-${Date.now()}`, {
      message: `${streamerLogins} ${
        newLiveStreams.length > 1 ? "are" : "is"
      } live`,
      type: "basic",
      title: "TFS",
      iconUrl: "../../images/icon-128.png",
    });
  }
};

const loginFunc = async (sendResponse: (result: LoginResult) => void) => {
  try {
    await login();
    await pollFetchFollowedStreams(true);
    sendResponse(Message.SUCCESS);
  } catch (error) {
    await clearData();
    sendResponse(Message.FAILURE);
  }
};

const logoutFunc = async (sendResponse: (result: LogoutResult) => void) => {
  await logout();
  await clearData();
  sendResponse(Message.SUCCESS);
};

const fetchAllFollowed = async (
  sendResponse: (result: FetchAllFollowedResult) => void
) => {
  try {
    const token = await getValueLS<string>("token");
    if (!token) {
      sendResponse({ message: Message.MUST_LOGIN });
      throw new Error("Must login");
    }
    const currentUserId = await getValueLS<string>("currentUserId");
    const allFollowedLogins = await fetchAllFollowedLogins(
      token,
      currentUserId
    );
    const usersInfos = await fetchUsersInfos(token, allFollowedLogins);
    sendResponse({
      message: Message.SUCCESS,
      followedUsersInfo: usersInfos,
    } as FetchAllFollowedResult);
  } catch (error: any) {
    if (error.message === "Invalid Token") {
      await clearData();
      sendResponse({ message: Message.FAILURE });
    } else {
      sendResponse({ message: Message.FETCH_ERROR });
    }
  }
};

chrome.runtime.onMessage.addListener((query: Query, _, sendResponse) => {
  switch (query) {
    case Query.LOGIN:
      loginFunc(sendResponse);
      break;
    case Query.LOGOUT:
      logoutFunc(sendResponse);
      break;
    case Query.FETCH_ALL_FOLLOWED:
      fetchAllFollowed(sendResponse);
      break;
    default:
      break;
  }
  return true;
});
