interface FavoriteStorage {
  userId: string;
  data: string[];
}
interface FavoriteStorageWithCurrentUserId {
  currentUserId: string;
  favorites: FavoriteStorage[];
}

export const getValueLS = async <T>(key: string): Promise<T> => {
  return new Promise((resolve, reject) => {
    try {
      chrome.storage.local.get(key, (value) => {
        resolve(value[key]);
      });
    } catch (err) {
      reject(err);
    }
  });
};

export const getValuesLS = async (
  keys: string[]
): Promise<{ [key: string]: any }> => {
  return new Promise((resolve, reject) => {
    try {
      chrome.storage.local.get(keys, (values) => {
        resolve(values);
      });
    } catch (err) {
      reject(err);
    }
  });
};

export const getFavoritesLS = async (): Promise<string[]> => {
  const lsValues = (await getValuesLS([
    "currentUserId",
    "favorites",
  ])) as FavoriteStorageWithCurrentUserId;
  const currentUserId = lsValues.currentUserId;
  const favorites = lsValues.favorites || [];
  const index = favorites.findIndex((f) => {
    return f.userId === currentUserId;
  });
  if (index !== -1) {
    return favorites[index].data;
  } else {
    return [];
  }
};

export const setFavoritesLS = async (newFavorites: string[]): Promise<void> => {
  const lsValues = (await getValuesLS([
    "currentUserId",
    "favorites",
  ])) as FavoriteStorageWithCurrentUserId;
  const currentUserId = lsValues.currentUserId;
  const favorites = lsValues.favorites || [];
  const index = favorites.findIndex((f) => f.userId === currentUserId);
  if (index !== -1) {
    favorites[index].data = newFavorites;
  } else {
    favorites.push({ userId: currentUserId, data: newFavorites });
  }
  await setValueLS("favorites", favorites);
};

export const setValueLS = async <T>(key: string, value: T): Promise<void> => {
  return new Promise((resolve, reject) => {
    try {
      chrome.storage.local.set({ [key]: value }, () => {
        resolve();
      });
    } catch (err) {
      reject(err);
    }
  });
};

export const deleteValueLS = async (keys: string | string[]): Promise<void> => {
  return new Promise((resolve, reject) => {
    try {
      chrome.storage.local.remove(keys, () => {
        resolve();
      });
    } catch (err) {
      reject(err);
    }
  });
};
