import type { IGetUser } from "./requests/interfaces";

export enum Query {
  LOGIN,
  LOGOUT,
  FETCH_ALL_FOLLOWED,
}

export enum Message {
  MUST_LOGIN,
  SUCCESS,
  FAILURE,
  FETCH_ERROR,
}

export type LogoutInput = Query.LOGOUT;
export type LogoutResult = Message.SUCCESS;

export type LoginInput = Query.LOGIN;
export type LoginResult = Message.SUCCESS | Message.FAILURE;

export type FetchAllFollowedInput = Query.FETCH_ALL_FOLLOWED;
export interface FetchAllFollowedResult {
  message: Message;
  followedUsersInfo?: IGetUser[];
}
