import { setValueLS } from "../utils/local_storage_promises";
import { fetchCurrentUserId } from "./fetchCurrentUserId";

export const login = async (): Promise<void> => {
  return new Promise(async (res, rej) => {
    let redirectUrl = chrome.identity.getRedirectURL();
    redirectUrl = redirectUrl.slice(0, redirectUrl.length - 1);
    chrome.identity.launchWebAuthFlow(
      {
        url: `https://id.twitch.tv/oauth2/authorize?client_id=${process.env.REACT_APP_CLIENT_ID}&redirect_uri=${redirectUrl}&force_verify=true&response_type=token&scope=user:read:follows`,
        interactive: true,
      },
      async (redirectUrl) => {
        try {
          const regex = /access_token=(.*?)&/g;
          if (!redirectUrl) {
            throw new Error("No redirect url");
          }
          const token = regex.exec(redirectUrl)?.[1];
          if (!token) {
            throw new Error("No token");
          }
          await setValueLS("token", token);
          const currentUserId = await fetchCurrentUserId(token);
          await setValueLS("currentUserId", currentUserId);
          res();
        } catch (error) {
          if (chrome.runtime.lastError) {
            // This error will not appear
            // https://stackoverflow.com/questions/28431505/unchecked-runtime-lasterror-when-using-chrome-api
          }
          rej();
        }
      }
    );
  });
};
