import { getValueLS } from "../utils/local_storage_promises";
import type { IGetUsers } from "./interfaces";

export const fetchCurrentUserId = async (token: string): Promise<string> => {
  const key = "userId";
  const userId = await getValueLS<string>(key);
  if (userId) {
    return userId;
  }
  const result = await fetch("https://api.twitch.tv/helix/users", {
    method: "GET",
    headers: {
      "client-id": process.env.REACT_APP_CLIENT_ID!,
      Authorization: `Bearer ${token}`,
    },
  });
  if (!result.ok) {
    throw new Error("Fetch Current User Id Failed");
  }
  const json: IGetUsers = await result.json();
  return json.data[0].id;
};
