import type { IGetUserFollow, IGetUsersFollows } from "./interfaces";

export const fetchAllFollowedLogins = async (
  token: string,
  currentUserId: string
): Promise<string[]> => {
  let cursor: string = "";
  let followsLogins: string[] = [];
  const fetchAllFollows = async (
    cursor: string,
    followsLogins: string[]
  ): Promise<string[]> => {
    const followsResult = await fetch(
      `https://api.twitch.tv/helix/channels/followed?user_id=${currentUserId}&first=100&after=${cursor}`,
      {
        method: "GET",
        headers: {
          "client-id": process.env.REACT_APP_CLIENT_ID!,
          Authorization: `Bearer ${token}`,
        },
      }
    );

    if (!followsResult.ok) {
      if (followsResult.status === 401) {
        throw new Error("Invalid Token");
      }
      throw new Error("Fetch All Follows Failed");
    }
    const followsJson: IGetUsersFollows = await followsResult.json();
    const currentFollowsLogins = followsJson.data.map(
      (e: IGetUserFollow) => e.broadcaster_login
    );
    if (followsJson.pagination?.cursor) {
      return await fetchAllFollows(followsJson.pagination.cursor, [
        ...followsLogins,
        ...currentFollowsLogins,
      ]);
    }
    return [...followsLogins, ...currentFollowsLogins];
  };
  followsLogins = await fetchAllFollows(cursor, followsLogins);
  return followsLogins;
};
